body {
  background-color: teal;
}

header {
  color: #f5f5f5;
  -webkit-box-shadow: 0 11px 5px #65abab;
  -moz-box-shadow: 0 11px 5px #65abab;
  background-color: #454a53;
  border-bottom: 3px solid #2f4f4f;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 14px;
  padding: 12px 0;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  box-shadow: 0 8px 25px #65abab;
}

form input {
  width: 150px;
  height: 32px;
  border-radius: 14px;
}

form button {
  width: 100px;
  height: 32px;
  border-radius: 14px;
}

form button:hover {
  background-color: #5f9ea0;
}

form button:active {
  background-color: teal;
}

.text-info {
  text-align: center;
  font-size: 12px;
}

.visually-hidden {
  visibility: hidden;
  overflow: hidden;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
  padding: 24px;
  display: flex;
}

.photo-link {
  width: 20%;
  text-decoration: none;
  display: block;
}

.photo-link:hover {
  cursor: -webkit-zoom-in;
  cursor: -moz-zoom-in;
  cursor: zoom-in;
}

.photo-link:hover img {
  transform: scale(1.5);
}

.photo-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 4px;
  transition: transform .25s linear;
}

.info {
  color: #fff;
  flex-flow: wrap;
  justify-content: center;
  column-gap: 2px;
  font-size: .5rem;
  display: flex;
}

.info-item {
  background-color: #3b3e4d;
  border-radius: 4px;
  margin: 1px 0;
  padding: 2px;
  display: inline-block;
}

.more-wrapper {
  justify-content: center;
  align-items: center;
  padding: 40px 0;
  display: flex;
}

.load-more {
  width: 75px;
  height: 75px;
  border: none;
  border-radius: 50%;
  transition: background-color .5s ease-in;
}

.load-more:hover {
  background-color: #87ceeb;
  transition: background-color .5s ease-in;
}

.load-more:active {
  color: #ff0;
  background-color: #454a53;
}

@media screen and (min-width: 768px) {
  header {
    gap: 24px;
    padding: 40px 0;
  }

  .info {
    font-size: 14px;
  }

  form input {
    width: 250px;
  }

  .photo-link {
    height: 150px;
  }

  .photo-card {
    height: 100px;
  }
}

@media screen and (min-width: 1200px) {
  .photo-link {
    height: 200px;
  }

  .photo-card {
    height: 150px;
  }
}

